<template>
  <div class="hs-container" v-if="details">
    <c-header v-if="Number($route.query.source) !== 1" isBackNav title="公告" fixedBackPath="/announcement/list"></c-header>
    <hs-scroll
      class="table-view-announcement"
      :data="[details]"
      :options="tableViewOptions">
      <ul>
        <div class="notice-view-item-detail">
          <div class="title">{{details.Title}}</div>
          <div class="time">{{details.CreateDate}}</div>
          <div class="content" v-html="details.NoticeContent"></div>
        </div>
      </ul>
    </hs-scroll>
  </div>
</template>
<script type="text/ecmascript-6">
import CHeader from 'components/c-header/c-header'
import HsCell1 from 'base/hs-tableview/hs-cell-1/hs-cell-1'
import HsNull from 'base/hs-null/hs-null'
import HsScroll from 'base/hs-scroll/hs-scroll'
import HsSection from 'base/hs-tableview/hs-section/hs-section'
export default {
  created() {
    if (!this.$route.params.details) {
      this.$router.replace('/home')
    }
  },
  data () {
    return {
      tableViewOptions: {
        pullDownRefresh: {
          threshold: 50
        }
      },
      details: this.$route.params.details
    }
  },
  components: {
    CHeader,
    HsCell1,
    HsNull,
    HsScroll,
    HsSection
  }
}
</script>
<style lang="stylus" rel="stylesheet/stylus">
@import '~common/stylus/base'
@import '~common/stylus/variable'
.hs-container
  width 100%
  height 100%
  >.table-view-announcement
    width 100%
    height calc(100% - 50px)
    overflow hidden
    background-color $color_white
.notice-view-item-detail
  padding 10px
  >.title
    font-size 1.2rem
    font-weight bold
    line-height 1.2
    margin-top 15px
  >.time
    margin 10px 0 20px
    font-size .8rem
    color $color_gray_placeholder
  >p
    line-height 1.3
    font-size .8rem
  >.content
    font-size .8rem
    overflow hidden
    img
      width 100%
      margin-bottom 5px
</style>



